import {ACTION_SWITCH_TO} from "../actions/course"

export const browser_hash  = store => next => action => {

  switch(action.type) {
    case ACTION_SWITCH_TO:
      window.history.pushState(null, "Blackboard", "/?course=" + store.getState().course.course + "&topic=" + action.topic + "&id=" + action.id)
  }

  return next(action)
}


